<template>
  <template-view>
    <section class="py-5">
      <div class="container">
        <div class="row">
          <div class="col-md-12 text-center">
            <div class="text-center py-3">
              <h1>{{ post.title }}</h1>
              <img :src="post.image" class="w-30" alt="" />
            </div>
            <div v-if="post.price == 'Free'">
              <div v-html="post.description"></div>
            </div>
            

            <div v-else>
              <div v-html="post.description.toString().substring(0, 500)"></div>
             <a href="/login" class="btn btn-danger">BUY NOW</a>
            </div>
          </div>
          <div class="row">
            <div class="col-md-4"></div>
            <div class="col-md-4 py-5">
          <div class="fb-like" data-href="https://sayari.com" data-width="500" data-layout="" data-action="" data-size="" data-share="true"></div>

            </div>
            <div class="col-md-4"></div>
          </div>
          <div
            class="fb-comments btn btn-tertiary text-dark"
            data-href="https://sayari.com"
            data-width="500"
            data-height="500"
            data-numposts="5"
          ></div>
        </div>
      </div>
    </section>
  </template-view>
</template>

<script>
import TemplateView from "@/views/TemplateView.vue";
import { mapGetters } from "vuex";
export default {
  components: { TemplateView },
  name: "PageView",
  props: ["id"],
  computed: {
    ...mapGetters({
      posts: "get_posts",
    }),

    post() {
      return this.posts.find((p) => p.id == this.id);
    },
  },
};
</script>

<style lang="scss" scoped></style>
